.App{

    height:calc(100%-40px);
    padding: 20px 0px;
    max-width: 600px;
    width: 600px;
}

.form{
   display: flex;
   gap:10px;
   justify-content: center;

}
.head{
    font-size: 45px;
    padding-bottom: 15px;
    font-weight:bold ;
    text-align: center;
    color:cornsilk;
}

.input{
    width: 400px;
    padding: 15px;
    border-radius: 4px;
    outline: none;
    border: none;
    font-size: 20px;

}

.button{
    padding:0px 20px ;
    border-radius:4px;
    border:none;
    font-size: 20px;
    font-weight:bold ;
    cursor: pointer;
    
}
.noteList{
max-height: 500px;
height: 500px;
margin: 0px;
overflow: auto;
margin-top: 30px;
list-style: none;
background-color: rgba(24,24,16,0.2);
border-radius:20px ;
padding: 20px;

}
.noteList li{
    color: #ffffff;
    font-size: 20px;
    border-bottom:1px solid #ffffff;
    display: flex;
    padding: 16px 0px ;
}
.noteList li label{
   flex: 1; 
}

.noteList li div{
  display:flex ;
  gap: 10px;  
}
.noteList li div button{
    padding:5px 10px;
    border-radius: 4px;
    border: none;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    color:#fff;
}
.noteList li div button:first-child{
    background: #14a44d;
    

}
.noteList li div button:last-child{
    background:rgb(220,76,100);
 
}
.noNotes{
   font-size: 30px;
   color: #fff; 
   text-align: center;
}